.tf-single-car-section{
    .tf-single-car-details-warper{
        padding: 32px 0;
        display: flex;
        gap: 32px;
        @media #{$tfmax991}{
            padding-left: 15px;
            padding-right: 15px;
        }
        @media #{$max767}{
            flex-wrap: wrap;
            padding-left: 15px;
            padding-right: 15px;
        }
        .tf-car-details-column{
            width: calc(100% - 472px);
            display: flex;
            gap: 32px;
            flex-wrap: wrap;
            @media #{$tfmax991}{
                width: calc(100% - 360px);
            }
            @media #{$max767}{
                width: 100%;
            }
            h3{
                color: #27333F;
                font-size: 21px;
                font-style: normal;
                font-weight: 400;
                line-height: 150%; /* 31.5px */
                margin-bottom: 16px;
                margin-top: 0px;
            }

            .tf-car-title{
                h1{
                    color: #27333F;
                    font-size: 28px !important;
                    font-style: normal;
                    font-weight: 600 !important;
                    line-height: 120%; /* 33.6px */
                }
                .breadcrumb{
                    ul{
                        margin: 0;
                        padding: 0;
                        li{
                            color: #27333F;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                            display: inline-block;
                            a{
                                text-decoration: none;
                                color: #27333F;
                                font-size: 16px;
                                font-weight: 400;
                            }
                        }
                    }
                }
            }

            .tf-car-hero-gallery{
                width: 100%;
                .tf-featured-car{
                    position: relative;
                    border-radius: 16px;
                    img{
                        border-radius: 16px;
                        height: 338px;
                        width: 100%;
                        object-fit: cover;
                    }
                    .tf-featured-reviews{
                        position: absolute;
                        top: 16px;
                        left: 16px;
                        a{
                            text-decoration: none;
                            border-radius: 8px;
                            background: #F8FAFC;
                            color: #566676;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                            span{
                                color: #566676;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                i{
                                    color: #FFBF0F;
                                    font-size: 18px;
                                }
                            }
                        }
                    }
                }
                .tf-gallery{
                    margin-top: 15px;
                    a{
                        width: calc(25% - 12px);
                        display: none;
                        height: 122px;
                        img{
                            border-radius: 8px;
                            object-fit: cover;
                            height: 100%;
                            width: 100%;
                        }
                        &.tf-gallery-more {
                            position: relative;
                
                            &::before{
                                border-radius: 8px;
                                content: "View More → ";
                                position: absolute;
                                color: #fff;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                top: 0;
                                bottom: 0;
                                left: 0;
                                right: 0;
                                width: 100%;
                                z-index: 1;
                                background: #0000009c;
                                font-size: $font-size-normal;
                                @media #{$tfmax767}{
                                    text-align: center;
                                    font-size: $font-size-xs;
                                }
                            }
                        }
                        &:nth-child(1),
                        &:nth-child(2),
                        &:nth-child(3),
                        &:nth-child(4){
                            display: block;
                        }
                    }   
                }
            }


            .tf-details-menu{
                ul{
                    margin: 0;
                    padding: 0;
                    li{
                        display: inline-block;
                        a{
                            text-decoration: none;
                            color: #27333F;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                            padding: 0 8px 8px 8px;
                            display: inline-block;
                        }
                        &.active{
                            a{
                                color: #0866C4;
                                border-bottom: 2px solid #0866C4;
                            }
                        }
                    }
                }
            }
            .tf-template-part{
                border-radius: 16px;
                background: #F5FAFF;
                padding: 8px 16px 16px 16px;
            }

            .tf-short-description{
                color: #566676;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 150%; /* 24px */
                p{
                    color: #566676;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 150% !important; /* 24px */
                    margin: 0;
                }
            }

            .tf-car-info{
                width: 100%;
                ul{
                    display: flex;
                    flex-wrap: wrap;
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    gap: 16px 8px;
                    li{
                        color: #566676;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 150%; /* 24px */
                        width: calc(25% - 8px);
                        border-radius: 8px;
                        border: 1px solid #DFE6ED;
                        padding: 8px;
                        i{
                            font-size: 20px;
                        }
                        @media #{$max575}{
                            width: calc(50% - 8px);
                        }
                    }
                }
            }

            .tf-car-benefits{
                width: 100%;
                ul{
                    display: flex;
                    flex-wrap: wrap;
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    gap: 16px 8px;
                    li{
                        color: #566676;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 150%; /* 24px */
                        width: calc(50% - 8px);
                        @media #{$max575}{
                            width: 100%;
                        }
                    }
                }
            }

            .tf-car-inc-exc-section{
                width: 100%;
                .tf-inc-exe{
                    .tf-inc-list,
                    .tf-exc-list{
                        width: 50%;
                        @media #{$max1024}{
                            width: 100%;
                        }
                        h3{
                            margin-bottom: 16px;
                        }
                        ul{
                            margin: 0;
                            padding: 0;
                            li{
                                border-radius: 6px;
                                background: radial-gradient(50% 50% at 50% 50%, #F5FAFF 0%, #E9F4FF 100%);
                                padding: 8px 16px 8px 12px;
                                color: #27333F;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                line-height: 150%; /* 24px */
                                i{
                                    color: #27BE69;
                                }
                            }
                        }
                    }
                    .tf-exc-list{
                        ul{
                            li{
                                i{
                                    color: #E51030;
                                }
                            }
                        }
                    }
                }
            }

            .tf-car-specification{
                width: 100%;
                ul{
                    display: flex;
                    flex-wrap: wrap;
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    gap: 16px;
                    li{
                        color: #566676;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 150%; /* 24px */
                        width: calc(50% - 8px);
                        border-bottom: 1px solid #DFE6ED;
                        span.label{
                            color: #27333F;
                            padding: 8px 16px;
                        }
                        span.value{
                            padding: 8px 16px;
                        }
                        @media #{$max575}{
                            width: 100%;
                            justify-content: space-between;
                        }
                    }
                }
            }

            .tf-car-location{
                width: 100%;
                .tf-car-location-map{
                    border-radius: 8px;
                    background: #EEF2F6;
                    height: 260px;
                    width: 100%;
                }
            }

            .tf-review-section{
                .tf-review-form-wrapper{
                    p{
                        margin: 0 0 20px 0;
                    }
                }
                .tf-review-form-container{
                    min-width: auto;
                    background:  transparent !important;
                    .tf-rating-wrapper{
                        margin-bottom: 20px;
                    }
                    .tf-review-submit{
                        input[type="submit"]{
                            padding: 16px 32px !important;
                            border-radius: 8px !important;
                            background: #0866c4 !important;
                            color: #fff !important;
                            font-size: 16px !important;
                            font-style: normal;
                            font-weight: 700;
                            line-height: 150%;
                            cursor: pointer;
                            border: 0px solid !important;
                        }
                    }
                }
                .tf-review-data{
                    margin-bottom: 32px;
                    .tf-review-data-average{
                        .avg-review{
                            color: #27333F;
                            font-size: 38px;
                            font-style: normal;
                            font-weight: 700;
                            line-height: 120%; /* 45.6px */
                            i{
                              font-size: 20px; 
                              color: #FFBF0F;
                            }
                        }
                        .tf-review-all-info{
                            p{
                                color: #566676;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                line-height: 150%; /* 24px */
                                margin: 0;
                            }
                        }
                    }
                }
                .tf-review-data-features {
                    .tf-percent-progress{
                        display: flex;
                        gap: 16px 32px;
                        flex-wrap: wrap;
                    }
                    .tf-progress-bar {
                        height: 5px;
                        background: #C0CCD8;
                        border-radius: 32px;
                    }
                    
                    .tf-progress-item {
                        width: calc(50% - 16px);
                        @media #{$tfmax767}{
                            width: 100%;
                        }
                        
                    }
                    
                    .tf-progress-bar {
                        position: relative;
                        margin-bottom: 8px;

                        .percent-progress {
                            position: absolute;
                            background: #0866C4;
                            top: 0;
                            left: 0;
                            bottom: 0;
                            border-radius: 7px;
                        }
                    }
                    
                    .tf-review-feature-label{
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-between;
                        p{
                            margin: 0;
                            color: #27333F;
                            font-size: 12px !important;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 120%; /* 14.4px */
                            text-transform: capitalize;
                            &.feature-rating{
                                font-weight: 700;
                            }
                        }
                    }
                }
                .tf-clients-reviews{
                    margin-top: 32px;
                    .tf-reviews-item{
                        .tf-reviews-avater{
                            img{
                                width: 56px;
                                height: 56px;
                                border-radius: 50%;
                            }
                        }
                        .tf-reviews-text{
                            .tf-review-rating{
                                font-size: 0;
                                color: #FFBF0F;
                                display: flex;
                                margin-bottom: 8px;
                                >div{
                                    display: flex;
                                    gap: 8px;
                                    i{
                                        font-size: initial;
                                    }
                                }
                            }
                            .tf-reviews-meta{
                                color: #27333F;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 700 !important;
                                line-height: 150%; /* 24px */
                                span{
                                    color:#566676;
                                    font-size: 16px;
                                    font-style: normal;
                                    font-weight: 400 !important;
                                    line-height: 150%; /* 24px */
                                }
                            }
                            p{
                                color: #566676;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                line-height: 150%; /* 24px */
                                margin: 15px 0 0 0;
                            }
                        }
                    }
                }
            }

            .tf-car-faq-section{
                width: 100%;
                .tf-faq-col{
                    margin-bottom: 16px;
                    &:last-child{
                        margin-bottom: 0px;
                    }
                    .tf-faq-head{
                        border-radius: 8px;
                        background: #FFF;
                        padding: 8px 16px;
                        cursor: pointer;
                        span{
                            color:#27333F;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                            gap: 8px;
                            flex-wrap: nowrap;
                            i{
                                color: #566676;
                            }
                        }
                    }
                    .tf-question-desc{
                        color:#27333F;
                        font-size: 15px;
                        padding: 8px 16px;
                        display: none;
                    }
                }
            }
        }

        .tf-car-booking-form{
            width: 440px;
            @media #{$tfmax991}{
                width: 400px;
            }
            @media #{$max767}{
                width: 100%;
            }
            .tf-price-header{
                h2{
                    color: #27333F;
                    font-size: 28px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 120%; /* 33.6px */
                    margin-bottom: 8px;
                    del{
                        color: #566676;
                        text-decoration-line: strikethrough;
                    }
                }
                p{
                    color: #566676;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 150%; /* 24px */
                    margin: 0;
                }
            }
            .tf-extra-added-info{
                border-radius: 16px;
                background: #EEF2F6;
                padding: 16px 24px 24px 24px;
                margin-bottom: 32px;
                display: none;
                .tf-extra-added-box{
                    h3{
                        color: #27333F;
                        font-size: 21px !important;
                        font-style: normal !important;
                        font-weight: 400 !important;
                        line-height: 150%; /* 31.5px */
                        margin: 0;
                    }
                    .tf-single-added-extra{
                        border-radius: 8px;
                        background: #FFF;
                        padding: 16px;
                        gap: 8px;
                        h4{
                            color: #27333F;
                            font-size: 16px !important;
                            font-style: normal !important;
                            font-weight: 400 !important;
                            line-height: 150% !important; /* 24px */
                            margin: 0;
                            width: calc(100% - 148px);
                        }
                        .qty-price{
                            color: #27333F;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                            width: 140px;
                            span.price{
                                margin-left: 10px;
                            }
                            i{
                                color: #566676;
                                font-size: 20px;
                            }
                            span.delete{
                                margin-left: 40px;
                                cursor: pointer;
                            }
                        }
                    }
                }
            }

            


            .tf-date-select-box{
                border-radius: 16px;
                background:#EEF2F6;
                padding: 8px 8px 16px 8px;
                margin-bottom: 32px;
                .tf-date-single-select{
                    border-radius: 8px;
                    background: #FFF;
                    padding: 16px;
                    margin-bottom: 8px;
                    &:last-child{
                        margin-bottom: 0px;
                    }
                    .tf-select-date{
                        width: calc(50% - 8px);
                        &:first-child{
                            border-right: 1px solid #DFE6ED;
                        }
                        
                        .icon{
                            svg{

                            }
                        }
                        
                        .info-select{
                            width: 100%;
                            position: relative;
                            h5{
                                color: #27333F;
                                font-size: 12px !important;
                                font-style: normal !important;
                                font-weight: 400 !important;
                                line-height: 120% !important; /* 14.4px */
                                margin-top: 0px;
                                margin-bottom: 8px;
                            }
                            input{
                                min-width: auto;
                                width: 100% !important;
                                color: #27333F;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                line-height: 150%; /* 24px */
                                border: 0px solid !important;
                                padding: 0;
                                &:focus{
                                    outline: none;
                                    box-shadow: none;
                                }
                                &::placeholder{
                                    color: #27333F;
                                    opacity: 1;
                                }
                            }
                            select{
                                min-width: auto;
                                width: 100% !important;
                                color: #27333F;
                                font-size: 16px;
                                font-style: normal;
                                font-weight: 400;
                                line-height: 150%; /* 24px */
                                border: 0px solid !important;
                                background-color: transparent;
                                padding: 0;
                            }
                            .autocomplete-items{
                                position: absolute;
                                border: 1px solid #d4d4d4;
                                border-bottom: none;
                                z-index: 99;
                                top: 100%;
                                left: 0;
                                right: 0;
                                max-height: 200px;
                                overflow: auto;
                                >div{
                                    padding: 10px;
                                    cursor: pointer;
                                    background-color: #fff;
                                    border-bottom: 1px solid #d4d4d4;
                                    color: #444;
                                    border-left: 1px solid #d4d4d4;
                                    border-right: 1px solid #d4d4d4;
                                    text-transform: capitalize;
                                }
                            }
                        }
                    }
                    
                }
                .tf-form-submit-btn{
                    .error-notice{
                        text-align: center;
                        color: red;
                        margin-bottom: 10px;
                    }
                    button{
                        padding: 16px 32px;
                        border-radius: 8px;
                        background: #0866C4;
                        color: #FFF;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: 150%; /* 24px */
                        cursor: pointer;
                        width: 100%;
                        border: 0px solid;
                    }
                }
                .tf-instraction-btn{
                    text-align: center;
                    a{
                        text-decoration: none;
                        color: #0866C4;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 150%; /* 24px */
                    }
                }
            }

            .tf-add-extra-section{
                border-radius: 16px;
                background: #EEF2F6;
                padding: 24px;
                margin-bottom: 32px;
                h3{
                    color: #27333F;
                    font-size: 21px !important;
                    font-style: normal !important;
                    font-weight: 400 !important;
                    line-height: 150% !important; /* 31.5px */
                    margin-bottom: 16px;
                    margin-top: 0px;
                }
                .tf-car-extra-infos{
                    .tf-car-single-extra{
                        padding: 12px 16px 16px 16px;
                        border-radius: 8px;
                        background: #F8FAFC;
                        .tf-extra-title{
                            h4{
                                color: #27333F;
                                font-size: 16px !important;
                                font-style: normal !important;
                                font-weight: 400 !important;
                                line-height: 150% !important; /* 24px */
                                margin-bottom: 8px;
                            }
                            .acr-select{
                                border-radius: 8px;
                                background: #E6F2FE;
                                padding: 4px 8px;
                                width: auto;
                                display: inline-flex;
                                gap: 10px;
                                height: 32px;
                                .acr-dec,
                                .acr-inc{
                                    color: #0866C4;
                                    border: 0px solid;
                                    background-color: transparent;
                                    width: auto;
                                    height: auto;
                                    padding: 0;
                                }
                                input{
                                    color: #566676;
                                    font-size: 16px;
                                    width: 18px !important;
                                    cursor: not-allowed;
                                }
                            }
                        }
                        .tf-extra-price{
                            h4{
                                color: #27333F;
                                font-size: 21px !important;
                                font-style: normal !important;
                                font-weight: 400 !important;
                                line-height: 150%; /* 24px */
                                margin-bottom: 8px;
                                small{
                                    display: block;
                                    font-size: 16px !important;
                                    color: #566676;
                                }
                            }
                        }

                    }
                    .tf-extra-apply-btn{
                        text-align: right;
                        button{
                            padding: 12px 32px;
                            border-radius: 16px;
                            background: #EEF2F6;
                            color:#0866C4;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 700;
                            line-height: 150%; /* 24px */
                            cursor: pointer;
                            border: 1px solid #0866C4;
                        }
                    }
                }
            }
            .tf-driver-details{
                border-radius: 16px;
                background: #EEF2F6;
                padding: 24px;
                .tf-driver-details-header{
                    h3{
                        color: #27333F;
                        font-size: 21px !important;
                        font-style: normal !important;
                        font-weight: 400 !important;
                        line-height: 150% !important; /* 31.5px */
                        margin: 0;
                    }
                    span{
                        color: #288FF6;
                        font-size: 16px !important;
                        font-style: normal !important;
                        font-weight: 400 !important;
                        line-height: 150% !important; /* 24px */
                        i{
                            color: #288FF6;
                        }
                    }
                }
                .tf-driver-photo{
                    border-bottom: 1px solid #C0CCD8;
                    padding-bottom: 16px;
                    img{
                        height: 56px;
                        width: 56px;
                        border-radius: 50%;
                    }
                    .tf-driver-info{
                        h4{
                            color: #27333F;
                            font-size: 16px !important;
                            font-style: normal !important;
                            font-weight: 700 !important;
                            line-height: 150% !important;
                            margin: 0;
                        }
                        p{
                            color: #27333F;
                            font-size: 16px !important;
                            font-style: normal !important;
                            font-weight: 400 !important;
                            line-height: 150% !important; /* 24px */
                            margin: 0;
                        }
                    }
                    
                }
                .tf-driver-contact-info{
                    ul{
                        padding: 0;
                        margin: 0;
                        li{
                            i{
                                font-size: 20px;
                            }
                            color: #566676;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 150%; /* 24px */
                        }
                    }
                }
            }
        }
    }
    .tf-car-conditions-section{
        padding-bottom: 100px;
        @media #{$tfmax991}{
            padding-left: 15px;
            padding-right: 15px;
        }
        @media #{$max767}{
            padding-left: 15px;
            padding-right: 15px;
        }
        h3{
            color: #27333F;
            font-size: 21px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%; /* 31.5px */
            margin-bottom: 16px;
        }
        table{
            border-left: 1px solid #DFE6ED;
            border-top: 1px solid #DFE6ED;
            margin: 0px;
            tr{
                th{
                    color: #27333F;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 150%; /* 24px */
                    border-right: 1px solid #DFE6ED;
                    border-bottom: 1px solid #DFE6ED;
                    padding: 16px;
                    vertical-align: baseline;
                    text-align: left;
                    width: 210px;
                }
                td{
                    color: #566676;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 150%; /* 24px */
                    border-right: 1px solid #DFE6ED;
                    border-bottom: 1px solid #DFE6ED;
                    padding: 16px 40px;
                }
            }
        }
    }
}